.flickity-enabled {
  position: relative;
}

.flickity-enabled:focus {
  outline: none;
}

.flickity-viewport {
  overflow: hidden;
  position: relative;
  height: 100%;
}

.flickity-slider {
  position: absolute;
  width: 100%;
  height: 100%;
}

.flickity-enabled.is-draggable {
  -webkit-tap-highlight-color: transparent;
  tap-highlight-color: transparent;
  user-select: none;
}

.flickity-enabled.is-draggable .flickity-viewport {
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
}

.flickity-enabled.is-draggable .flickity-viewport.is-pointer-down {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

.flickity-button {
  position: absolute;
  border: none;
  color: #333;
  background: transparent;
  opacity: 1;
  transition: transform 0.2s;
}

.flickity-button:hover {
  cursor: pointer;
}

.flickity-button:hover .flickity-button-icon {
  transform: scale(1.05);
}

.flickity-button:focus {
  outline: none;
}

.flickity-button:disabled {
  opacity: 0.1;
  cursor: auto;
  pointer-events: none;
}

.flickity-button-icon {
  fill: currentColor;
  transition: transform 0.2s;
}

.flickity-prev-next-button {
  top: 50%;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  transform: translateY(-50%);
}

.flickity-prev-next-button.previous {
  left: 10px;
}

.flickity-prev-next-button.previous:active {
  transform: translate(-5px, -50%);
}

.flickity-prev-next-button.next {
  right: 10px;
}

.flickity-prev-next-button.next:active {
  transform: translate(5px, -50%);
}

.flickity-prev-next-button .flickity-button-icon {
  position: absolute;
  left: 20%;
  top: 20%;
  width: 60%;
  height: 60%;
}

.flickity-page-dots {
  position: relative;
  width: 100%;
  bottom: 0;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 800px;
  left: 50%;
  transform: translateX(-50%);
}

.flickity-page-dots .dot {
  display: block;
  max-width: 12px;
  width: 100%;
  height: 30px;
  margin: 0 8px;
  position: relative;
  color: currentColor;
  cursor: pointer;
  transition: max-width 0.8s ease;
}

.flickity-page-dots .dot:first-child:last-child {
  display: none;
}

.slider--autoplay .flickity-page-dots .dot {
  flex: 1;
  max-width: 100px;
}

.flickity-page-dots .dot::before, .flickity-page-dots .dot::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  margin-top: -1px;
  left: 0;
  right: 0;
  height: 12px;
  border-radius: 6px;
  background: currentColor;
}

.slider--autoplay .flickity-page-dots .dot::before, .slider--autoplay .flickity-page-dots .dot::after {
  height: 3px;
  border-radius: 0;
}

.flickity-page-dots .dot::before {
  opacity: 0.4;
  transition: opacity 0.2s linear, border-radius 0.3s ease 0.5s, height 0.3s ease 0.5s;
}

.flickity-page-dots .dot::after {
  opacity: 0;
  transform-origin: left;
  transition: border-radius 0.3s ease 0.5s, height 0.3s ease 0.5s;
}

.slider--autoplay .flickity-page-dots .dot::after {
  transform: scaleX(0);
  transition: transform 6s linear, border-radius 0.3s ease 0.5s, height 0.3s ease 0.5s;
}

.flickity-page-dots .dot:hover::before {
  opacity: 0.8;
}

.flickity-page-dots .dot.is-selected::after {
  opacity: 1;
}

.slider--init .flickity-page-dots .dot.is-selected::after {
  transform: scaleX(1);
}

.flickity-enabled.is-fade .flickity-slider > * {
  pointer-events: none;
  z-index: 0;
}

.flickity-enabled.is-fade .flickity-slider > .is-selected {
  pointer-events: auto;
  z-index: 1;
}

.slider-wrapper {
  position: relative;
}

.slider {
  opacity: 0;
  transition: opacity 0.4s;
}

.slider.flickity-enabled {
  opacity: 1;
}

.slider__slide {
  width: 100%;
  margin-right: 10px;
}

a.slider__slide:hover {
  text-decoration: none;
  outline: none;
}

.slider--page,
.slider-wrapper--page {
  padding: 0;
}

@media (min-width: 1024px) {
  body:not(.neos-backend) .slider--page .flickity-slider, body:not(.neos-backend)
  .slider-wrapper--page .flickity-slider {
    transform: none !important;
  }
}

.slider--page .flickity-page-dots,
.slider-wrapper--page .flickity-page-dots {
  bottom: 10px;
  position: absolute;
}

.slider--page .dot,
.slider-wrapper--page .dot {
  color: #333;
}

.slider__slide--page,
.slider__slide--page picture,
.slider__slide--page img {
  width: 100%;
  display: block;
}

body:not(.neos-backend) .slider__slide--page {
  height: 0;
  padding-top: 100%;
}

body:not(.neos-backend) .slider__slide--page picture {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

@media (min-width: 768px) {
  body:not(.neos-backend) .slider__slide--page {
    padding-top: 50%;
  }
}

@media (min-width: 1024px) {
  body:not(.neos-backend) .slider__slide--page {
    padding-top: 33.3333333333%;
  }
}

@media (min-width: 1024px) {
  body:not(.neos-backend) .slider__slide--page {
    left: 0 !important;
    opacity: 0;
    transition: opacity 0.5s linear;
    pointer-events: none;
  }
  body:not(.neos-backend) .slider__slide--page.is-selected {
    opacity: 1;
    pointer-events: auto;
  }
}
